<template>
    <div>  
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">班级简介设置</p>
            <span class="f26 cfff pr-30" style="position:absolute;top:0;right:0;line-height:0.84rem;z-index:99;" @click="save">保存</span>
        </div>
        <div class="c333 f28" style="padding:0.84rem 0 1rem;">
            <div class="nameBox">
                <div class="pt-90 pb-90 bs">
                    <img src="../../assets/class/upLoadImg.png" alt="">
                    <p class="pt-60 cccc f32">上传封面</p>
                </div>
            </div>
            <div class="content b_line">
                <p class="subtitle">班级简介</p>
                <textarea class="c666" placeholder="请输入简介内容..."></textarea>
            </div>
            <div class="content">
                <p class="subtitle">图片</p>
                <div class="picture">
                    <img src="../../assets/my/upload.png" alt="">
                </div>
                <p class="subtitle">视频</p>
                <div class="picture">
                    <img src="../../assets/my/upload.png" alt="">
                </div>
            </div>  
        </div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            data:{}
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        save(){
            this.$router.push({path:"/classIntro"});
        }
    }
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .nameBox{
        padding:.3rem;
    }
    .nameBox div{
        width:100%;
        height:3.88rem;
        box-shadow: 0 0 0.3rem #ccc;
        border-radius: 0.1rem;
        text-align: center;
    }
    .nameBox img{
        width:1rem;
    }
    .name{
        border:0;
        text-align: right;
        width:50%;
    }
    .content{
        padding:0 .3rem .3rem;
    }
    .subtitle{
        line-height: .9rem;
    }
    .content textarea{
        width:100%;
        height:2.67rem;
        border:0;
    }
    .picture img{
        width:1.2rem;
        height:1.2rem;
        margin-bottom:.1rem;
    }
</style>




